/*toggleMenu*/
.mainMenu.hide {
    margin-left: -150px;
}

.mainMenu {
    margin-left: 0px;
    width: 100px;
    height: 100vh;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    transition: .3s
}

.mainMenu li {
    position: relative;
    text-align: center;
    flex-grow: 1;
}

.mainMenu li img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 40%;
}

.mainMenu li a {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: black;
    filter: blur(1px);
    box-shadow: 0 1px 0 black, 2px 0 2px black;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.1);
}

.mainMenu li a.active {
    border-right: 0px solid black;
    filter: blur(0px);
    box-shadow: 0 0 0 0 black;
    padding-left: -10px;
    box-shadow: -17px 3px 15px black;
    background-color: transparent;
}

.mainMenu li a span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)
}


@media (min-width: 1024px) {
    .mainMenu.hide {
        margin-left: 0px;
    }

}